export default{
	data:function(){
		return {
			phoneClass:'',
			codeClass:''
		}
	},
	mounted(){
		uni.setNavigationBarTitle({
		　　title:'手机登录'
		})
	},
	methods:{
		phoneFocus(e){
			this.phoneClass="focus"
		},
		phoneBlur(e){
			this.phoneClass=""
		},
		codeFocus(e){
			this.codeClass="focus"
		},
		codeBlur(e){
			this.codeClass=""
		},
		login(){
			uni.switchTab({
				url:"../index/index"
			})
		}
	}
}
